.dashboard {
    padding: 6rem 2rem 4rem;
    max-width: 1200px;
    margin: 0 auto;
}

.title {
    font-size: 2rem;
    margin-bottom: 2rem;
}

.subtitle {
    font-size: 1.5rem;
    margin-bottom: 1.5rem;
    margin-top: 3rem;
}

.overview {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
    gap: 1.5rem;
}

.card {
    background-color: rgba(255, 255, 255, 0.05);
    border: 1px solid rgba(255, 255, 255, 0.1);
    border-radius: 8px;
    padding: 1.5rem;
}

.card h3 {
    font-size: 1rem;
    color: var(--tesla-gray);
    margin-bottom: 0.5rem;
    font-weight: 500;
}

.amount {
    font-size: 1.8rem;
    font-weight: 600;
}

.positive {
    color: #4caf50;
}

.negative {
    color: #f44336;
}

.portfolio {
    display: flex;
    flex-direction: column;
    gap: 1rem;
}

.planRow {
    background-color: rgba(255, 255, 255, 0.03);
    border: 1px solid rgba(255, 255, 255, 0.05);
    border-radius: 8px;
    padding: 1.5rem;
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-wrap: wrap;
    gap: 1rem;
}

.planInfo h4 {
    font-size: 1.2rem;
    margin-bottom: 0.3rem;
}

.date {
    font-size: 0.8rem;
    color: var(--tesla-gray);
}

.planStats {
    display: flex;
    gap: 2rem;
}

.statGroup {
    display: flex;
    flex-direction: column;
    align-items: flex-end;
}

.label {
    font-size: 0.8rem;
    color: var(--tesla-gray);
    margin-bottom: 0.2rem;
}

.loading {
    padding: 6rem 2rem;
    text-align: center;
    font-size: 1.2rem;
    color: var(--tesla-gray);
}

@media (max-width: 768px) {
    .planRow {
        flex-direction: column;
        align-items: flex-start;
    }

    .planStats {
        width: 100%;
        justify-content: space-between;
    }

    .statGroup {
        align-items: flex-start;
    }
}